@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;font-weight:400;vertical-align:baseline;background:transparent}body{line-height:1}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}nav ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}a{margin:0;padding:0;font-size:100%;vertical-align:baseline;background:transparent}ins{background-color:#ff9;color:#000;text-decoration:none}mark{background-color:#ff9;color:#000;font-style:italic;font-weight:bold}del{text-decoration:line-through}abbr[title],dfn[title]{border-bottom:1px dotted;cursor:help}table{border-collapse:collapse;border-spacing:0}hr{display:block;height:1px;border:0;border-top:1px solid #cccccc;margin:1em 0;padding:0}input,select{vertical-align:middle}body{font-family:Roboto, sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.app {
  min-height: 100vh;
  background: rgb(0,140,140);
  background: linear-gradient(0deg, rgba(0,140,140,1) 0%, rgba(0,56,90,1) 100%);
}

.card{background-color:#fff;padding:0.5rem;border-radius:5px;margin:0.5rem 0;font-size:1rem}

.list{-webkit-flex:0 0 400px;flex:0 0 400px;padding:20px;margin:0 20px;border-radius:5px;background-color:#dcdde2}.list .list__title{font-size:1.5rem;padding-bottom:1rem;font-weight:400}.list .list__card-add-button{font-family:Roboto, sans-serif;font-size:1rem;background-color:#fff;border-radius:5px;width:100%;padding:0.5rem;border:none}.list .list__card-add-button:hover{background-color:#aaa}

.board .board__header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap}.board .board__header .board__title{font-size:3rem;color:#ffffff;display:inline-block;padding:1rem 2rem;line-height:3rem}.board .board__header .board__list-add-button{border:none;border-radius:5px;background-color:#ddd;height:2.5rem;font-size:1.5rem}.board .board__header .board__list-add-button:hover{background-color:#aaa}.board .board__list-container{display:-webkit-flex;display:flex;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;overflow-x:auto;padding:1rem}@media (max-width: 980px){.board__header{-webkit-justify-content:center;justify-content:center}.board__header .board__title{text-align:center;width:100%}}

